@mixin media-xsmall { // for-phone-only {
  @media (max-width: 599px) { @content; }
}
@mixin media-small { // for-tablet-portrait-up {
  @media (min-width: 600px)
     and (max-width: 899px) { @content; }
}
@mixin media-medium { // for-tablet-landscape-up {
  @media (min-width: 900px)
     and (max-width: 1199px) { @content; }
}
@mixin media-large { // for-desktop-up {
  @media (min-width: 1200px)
     and (max-width: 1799px) { @content; }
}
@mixin media-xlarge { // for-big-desktop-up {
  @media (min-width: 1800px) { @content; }
} 

