$duration: .8s;

.crs_scene {
  position: absolute;
  left: 50%;
  top:  40%;
  //margin: auto;
  //margin-top: 18vh;
  perspective: var(--pp, 800px);  // distance from the viewer to the object
  //border-radius: 50%;
  border: 1px dashed rgba(255,255,0,.5);  // #debug
  transition: transform $duration ease-in;
}

.crs_body {
  width:  100%;
  height: 100%;
  transform-style: preserve-3d;
  transition: transform $duration ease-in;
  border: 2px dotted rgba(255,0,255,.5);  // #debug
  background: rgba(255,0,255,.2);   // #debug
  // animation: solar-term-rotate 240s linear infinite;
}

.crs_cell {
  position: absolute;
  width:  100%;
  height: 100%;
  //vertical-align: middle; // only applies to inline-level and 'table-cell' elements, use line-height
  /*
  height: 60px;
  left: 10px;
  top: 10px;
  //line-height: 55px;
  font-size: 36px;
  */
  //border: 1px solid black;
  //text-shadow: 1px 1px 2px darkgray;
  //color: black;
  font-weight: bold;
  //@include textOutline(gray);
  //transition: transform 1s, opacity 1s;
  //backface-visibility: visible;
  box-shadow: 1px 1px 4px 1px black; // offset-x, offset-y, blur-radius, spread-radius 
  //border: 'none';
  border-radius: $cell-bdr;
  background: rgba(250,250,250,var(--cell_op,.8));  // fallback
  //background: rgba(var(--cell_cl,220),var(--cell_cl,220),var(--cell_cl,220),var(--cell_op,.8));
  transition: transform $duration ease-in, background $duration/2 ease-in;
  //background: linear-gradient(to right, red, orange, yellow, green, blue, indigo, violet);
  //will-change: transform, background; // hints to browsers how an element is expected to change
  &:hover {
    box-shadow: 1px 1px 4px 1px black, 0 0 12px 8px #0ff;
  }
}

.shadow_ff {
  box-shadow: 1px 1px 4px 1px black, 0 0 8px 4px #ffd700;
  transition: transform $duration/4 ease-in;
}

.inner-space {
  position: absolute;
  transform-style: preserve-3d;
  width: 100%;
  left: var(--in_left);  // After setting width:100%, - <svg> width (independent of cell size)
  top:  var(--in_top);   // - <svg> height + crs_body height / 2 (height:100% not working)
}

$sun_op : .5;  // opacity
// To center an elementa
// 1. position: absolute 2. apply left: 50% 3. margin-left = half of the element width
.sun  {
  --sun_r: 8px;  // radius of the sun, default value to be updated in JavaScript
  position: absolute;
  left: 50%;
  top:  50%;
  width:   calc(var(--sun_r) * 2);
  height:  calc(var(--sun_r) * 2);
  margin-left: calc(-1 * var(--sun_r));
  margin-top:  calc(-1 * var(--sun_r));
  border-radius: 50%;
  background: white;
  box-shadow: 0 0 6px 2px #fbe602, 0 0 10px 4px #e70f14, 0 0 14px 6px #fabc14;
  &:hover {
    box-shadow: 0 0 26px 32px rgba(251,230,2,$sun_op), 0 0 30px 34px rgba(231,15,20,$sun_op), 0 0 44px 48px rgba(250,188,20,$sun_op);
  }
  transition: all .5s ease-in;
  //box-shadow: 0px 0px 5px 6px orange;  // offset-x, offset-y, blur-radius, spread-radius
//  animation: sun-glow 1s linear infinite alternate;
}


