%sekki-name-common {  // 二十四節気
  text-align: center;
  cursor: pointer;
  font-size: 2vw;
  transition: background .3s ease-in;
  text-shadow: .4px .4px 0 gold, -.3px -.3px 0 gray;
}

.sekki-name {  // 二十四節気
  @extend %sekki-name-common;
  border-radius: $cell-bdr $cell-bdr 0 0;
}

.sekki-name_v {  // vertical, left column
  @extend %sekki-name-common;
  float: left;
  writing-mode: vertical-lr;
  height: 100%;
  border-radius: $cell-bdr 0 0 $cell-bdr;
}

.sekki-emoji {
  font-size: 1.4vw;
  padding: .4vw;
  text-shadow: 1px 1px 2px dimgray, -1px -1px 2px dimgray;
}

%s72-names-common {  // 七十二候
  font-size:   1.7vw;  // vw = 1% of viewport width
  line-height: 1.4vw;
  white-space: nowrap;
  .s72-yomi {
    font-size: .5vw;
    line-height: 1.5vw;
    color: navy;
  }
  cursor: pointer;
  &:hover {
    text-shadow: 0 0 6px  #fff, 0 0 10px #e60073;
  }
}

.s72-names {  // 七十二候
  float: left;
  width: 33.33%;   // 3 columns
  writing-mode: vertical-rl;  // right-to-left
  text-align: start;  // align to top, https://24ways.org/2016/css-writing-modes/
  padding-top: 6px;
  padding-bottom: 6px;
//  border: .5px solid black;
  @extend %s72-names-common;
}

.s72-names_v {  // vertical, right column
  text-align: left;
  height: 33.3%;
  @extend %s72-names-common;
}

@mixin btnDemo($borderColor) {
  background-color: Gainsboro;
  border: 2px solid $borderColor;
  color: black;
  padding: 4px 4px;
  font: small "Courier New", sans-serif;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 10px;
  margin: 1px 2px;
  z-index: 100;
}

.btn-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-left: 10px;
  padding-top: 20px;
}

.test-btn {
  left: 10px;
  top:  10px;
  padding: 4px;
  margin:  2px;
  border-radius: 10%;
  @include btnDemo(#4caf50);
}

.test-btn:disabled,
test-btn[disabled] {
  border: 1px solid olive;
  background-color: darkgray;
  color: SlateGray;
}

.debug-out {
  position: absolute;
  background: rgba(220,20,60,.3);
  width: 100%;
  text-align: center;
  font-family: 'Courier New';
  a {
    color: White;
    &:visited {
      color: yellow;
    }
  }
}
