  box-shadow: 1px 1px 4px 1px black;
  border-radius: 5px;
  background: rgba(250, 250, 250, 0.8);
  background: rgba(250, 250, 250, var(--cell_op, 0.8));
  -webkit-transition: background 0.4s ease-in, -webkit-transform 0.8s ease-in;
  transition: background 0.4s ease-in, -webkit-transform 0.8s ease-in;
  transition: transform 0.8s ease-in, background 0.4s ease-in;
  transition: transform 0.8s ease-in, background 0.4s ease-in, -webkit-transform 0.8s ease-in; }
  .crs_cell:hover {
    box-shadow: 1px 1px 4px 1px black, 0 0 12px 8px #0ff; }

.shadow_ff {
  box-shadow: 1px 1px 4px 1px black, 0 0 8px 4px #ffd700;
  -webkit-transition: -webkit-transform 0.2s ease-in;
  transition: -webkit-transform 0.2s ease-in;
  transition: transform 0.2s ease-in;
  transition: transform 0.2s ease-in, -webkit-transform 0.2s ease-in; }

.inner-space {
  position: absolute;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  width: 100%;
  left: var(--in_left);
  top: var(--in_top); }

.sun {
  --sun_r: 8px;
  position: absolute;
  left: 50%;
  top: 50%;
  width: calc(var(--sun_r) * 2);
  height: calc(var(--sun_r) * 2);
  margin-left: calc(-1 * var(--sun_r));
  margin-top: calc(-1 * var(--sun_r));
  border-radius: 50%;
  background: white;
  box-shadow: 0 0 6px 2px #fbe602, 0 0 10px 4px #e70f14, 0 0 14px 6px #fabc14;
  -webkit-transition: all .5s ease-in;
  transition: all .5s ease-in; }
  .sun:hover {
    box-shadow: 0 0 26px 32px rgba(251, 230, 2, 0.5), 0 0 30px 34px rgba(231, 15, 20, 0.5), 0 0 44px 48px rgba(250, 188, 20, 0.5); }

